// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueCordova from 'vue-cordova'
import VueResource from 'vue-resource'
import VueLocalStorage from 'vue-localstorage'

import Mint from 'mint-ui'
import VCharts from 'v-charts'
import '../node_modules/mint-ui/lib/style.css'
import './assets/css/def.css'

//
// import VueSweetalert2 from 'vue-sweetalert2'

//
Vue.use(Mint)
Vue.use(VCharts)
Vue.use(VueCordova)
Vue.use(VueResource)
Vue.use(VueLocalStorage)
// Vue.use(VueSweetalert2)

//
Vue.config.productionTip = false

//
Vue.http.options.root = process.env.API
Vue.http.interceptors.push(function (_request, _next) {
  Vue.http.headers.common['OAUTH-API-KEY'] = this.$root.$data.api_key
})

/* eslint-disable no-new */
import { Toast } from 'mint-ui'
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  data: {
    statusStr: '載入中',
    api_key: '',
    loginStatus: 0,
    position: {},
    systemName: 'rollcall',
    systemVersion: '1.0',
    network_status: true,
    api_url: process.env.API,
    pub_url: process.env.BASE_URL
  },
  created: function () {
    // this.checkStatus()
    this.watchEven()
  },
  methods: {
    // 確認 Token 資訊
    checkStatus: function () {
      console.log('啟動自我檢測系統...')
      // 獲取已經拿到的 API TOKEN
      var getkey = Vue.localStorage.get('api_key')
      // 是否需要重拿的參數
      var reGetKey = false
      // 判斷是否需要重拿
      if (getkey) {
        if (getkey.length === 0) {
          reGetKey = true
        }
      } else {
        reGetKey = true
      }

      if (reGetKey) {
        console.log('判斷需要重新獲取API TOKEN')
        this.$http.get('member/getStatus', {
          params: {
            ver: 'app',
            num: 1
          }
        }).then(function (_response) {
          if (_response.body.error_code === 0) {
            console.log('###################################')
            console.log('原有API Token:' + getkey)
            console.log('取得API Token:' + _response.body.api_key)
            this.$data.api_key = _response.body.api_key
            console.log('置換API Token:' + this.$data.api_key)
            Vue.localStorage.set('api_key', this.$data.api_key)
            console.log('###################################')
          }
        }, function (_response) { })
      } else {
        this.$data.api_key = getkey
        console.log('###################################')
        console.log('原有API Token:' + getkey)
        console.log('沿用API Token:' + getkey)
        console.log('###################################')
      }
    },
    // 提報裝置 Log
    setStatistics: function (_logText = "") {
      // this.$http.get('broadcast-setStatistics', {
      //     params: {
      //         articleId: 0,
      //         useType: _setType
      //     }
      // }).then(function (_response) { }, function (_response) { })
    },
    // 啟用監測裝置
    watchEven: function () {
      // 監聽按鈕
      Vue.cordova.on('deviceready', () => {
        console.log("<================ cordova ================>")
        console.log("<================ status: " + Vue.cordova.deviceready)
        console.log("<================ +++++++ ================>")
        console.log('啟用背景監聽 cordova 各種事件...')
        // this.getLocation()
      })
      Vue.cordova.on('backbutton', () => {
        if (confirm('確定離開程式嗎？')) {
          this.setStatistics(5)
          navigator.app.exitApp()
        }
      })
      Vue.cordova.on('offline', () => {
        this.$data.network_status = false
        alert('網路好像斷線惹')
      })
      Vue.cordova.on('online', () => {
        this.$data.network_status = true
      })
    },
    // 提請取得裝置位置
    getLocation: function () {
      navigator.geolocation.getCurrentPosition(this.setLocation, this.errLocation);
    },
    // 當位置可正常取得時
    setLocation: function (position) {
      this.$data.position = position
      console.log("###=> setLocation")
      console.log('Latitude: ' + position.coords.latitude)
      console.log('Longitude: ' + position.coords.longitude)
      console.log('Altitude: ' + position.coords.altitude)
      console.log('Accuracy: ' + position.coords.accuracy)
      console.log('Altitude Accuracy: ' + position.coords.altitudeAccuracy)
      console.log('Heading: ' + position.coords.heading)
      console.log('Speed: ' + position.coords.speed)
      console.log('Timestamp: ' + position.timestamp)
      this.reportLocation(position.coords.latitude, position.coords.longitude)
    },
    // 提報裝置啟用位置
    reportLocation: function (_latitude = '', _longitude = '') {
      this.$http.post('rollcall-setDeviceLocation',
        {
          api_key: this.$data.api_key,
          systemName: this.$data.systemName,
          latitude: _latitude,
          longitude: _longitude,
        }
      ).then(function (_response) { })
    },
    // 當位置取得發生錯誤時
    errLocation: function (error) {
      console.log("###=> setLocation is Error.[" + error.code + "]" + error.message)
      var vm = this
      if (error.code === 1) {
        this.showAlert("請授權裝置使用您的位置以啟用應用程序")
        setTimeout(() => { vm.getLocation() }, 3000);
      }
    },
    // 顯示提示資訊
    showAlert: function (_message = '', _type = 1) {
      if (_type === 1) {
        Toast({
          message: _message,
          position: 'bottom',
          duration: 5000
        })
      }
    }
  }
})



